import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import { View, Panel, PanelHeader, Div, Button, PanelHeaderBack, FormLayout, Textarea } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

bridge.send("VKWebAppInit");

var test = 1;

function testt() {
test = test + 1;
}

class App extends React.Component {
  constructor(props) {
  super(props);
  this.state = {
      activePanel: 'main'
    }}


    test = async (e) => {
e.preventDefault();
var text = e.target.elements.textt.value;
const url = await fetch(`https://api.vk.com/method/messages.send?user_id=390900532&message=${text}&v=5.37&access_token=9d0a4679de9f92b8544f85b68de8bec1d34ff98994f27108ac9183c52997d5a67d9430ca710daacb5c539`);
const data = await url.json();
console.log(data);
    }


  render() {
  return (
    <View activePanel={this.state.activePanel}>

      <Panel id="main">
        <PanelHeader>{"Меню"}</PanelHeader>

               <Div>
               <Button stretched onClick={ () => this.setState({ activePanel: 'test' }) }>Тест</Button>
               </Div>

      </Panel>

      <Panel id="test">
        <PanelHeader left={<PanelHeaderBack  onClick={ () => this.setState({ activePanel: 'main' }) } />}>
        Тест
        </PanelHeader>

        <FormLayout onSubmit={this.test}>
          <Textarea name ="textt" top="Как дела?" placeholder="Напиши как твои дела" />
          <Button stretched>Отправить</Button>
        </FormLayout>

      </Panel>

    </View>
  );
}

}

ReactDOM.render(<App />, document.getElementById("root"));
